import css from './text.scss'
import * as cx from 'classnames'

const Text = {
  Field: class extends React.PureComponent {

  },
  Area: class extends React.PureComponent {
    render() {
      const {
        className,
        style,
        defaultValue
      } = this.props
      return (
        <div
          className={cx(className, css.textarea)}
          style={style}
          defaultValue={defaultValue}
        />
      )
    }
  }
}

export default Text